import App from '@/App';
export default class SetEconomyFurnitureEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('economy/addFurnitureNames', data.furnitureNames);
        App.interfaceManager.container.$store.commit('economy/addFurnitureAmounts', data.furnitureAmounts);
        App.interfaceManager.container.$store.commit('economy/addFurnitureImages', data.furnitureImages);
        App.interfaceManager.container.$store.commit('economy/addFurnitureCategories', data.furnitureCategories);
        App.interfaceManager.container.$store.commit('economy/addFurniturePointsTypes', data.furniturePointsTypes);
        App.interfaceManager.container.$store.commit('economy/addFurniturePointsAmounts', data.furniturePointsAmounts);
    }
}
