import App from '@/App';
export default class OpenEconomyEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('economy/setCategoryIds', data.categoryIds);
        App.interfaceManager.container.$store.commit('economy/setCategoryNames', data.categoryNames);
        App.interfaceManager.container.$store.commit('economy/setCategoryIcons', data.categoryIcons);
        App.interfaceManager.container.$store.commit('economy/setOpen', true);
        App.interfaceManager.container.$store.commit('economy/setFurniture', true);
    }
}
