import Eco from '@/store/models/Eco';
const state = {
    open: false,
    categoryIds: [],
    categoryNames: [],
    categoryIcons: [],
    furnitureNames: [],
    furnitureAmounts: [],
    furnitureImages: [],
    furnitureCategories: [],
    furniturePointsTypes: [],
    furniturePointsAmounts: [],
    eco: []
};
const namespaced = true;
const mutations = {
    setCategoryIds(state, categoryIds) {
        state.categoryIds = categoryIds;
    },
    setCategoryNames(state, categoryNames) {
        state.categoryNames = categoryNames;
    },
    setCategoryIcons(state, categoryIcons) {
        state.categoryIcons = categoryIcons;
    },
    addFurnitureNames(state, furnitureNames) {
        for (let i = 0; i < furnitureNames.length; i++) {
            state.furnitureNames.push(furnitureNames[i]);
        }
    },
    addFurnitureAmounts(state, furnitureAmounts) {
        for (let i = 0; i < furnitureAmounts.length; i++) {
            state.furnitureAmounts.push(furnitureAmounts[i]);
        }
    },
    addFurnitureImages(state, furnitureImages) {
        for (let i = 0; i < furnitureImages.length; i++) {
            state.furnitureImages.push(furnitureImages[i]);
        }
    },
    addFurnitureCategories(state, furnitureCategories) {
        for (let i = 0; i < furnitureCategories.length; i++) {
            state.furnitureCategories.push(furnitureCategories[i]);
        }
    },
    addFurniturePointsTypes(state, furniturePointsTypes) {
        for (let i = 0; i < furniturePointsTypes.length; i++) {
            state.furniturePointsTypes.push(furniturePointsTypes[i]);
        }
    },
    addFurniturePointsAmounts(state, furniturePointsAmounts) {
        for (let i = 0; i < furniturePointsAmounts.length; i++) {
            state.furniturePointsAmounts.push(furniturePointsAmounts[i]);
        }
    },
    setFurniture(state, furniture) {
        state.eco = [];
        for (let i = 0; i < state.furnitureNames.length; i++) {
            state.eco.push(new Eco(state.furnitureNames[i], state.furnitureAmounts[i], state.furnitureImages[i], state.furnitureCategories[i], state.furniturePointsTypes[i], state.furniturePointsAmounts[i]));
        }
        state.furnitureNames = [];
        state.furnitureAmounts = [];
        state.furnitureImages = [];
        state.furnitureCategories = [];
        state.furniturePointsTypes = [];
        state.furniturePointsAmounts = [];
    },
    setOpen(state, open) {
        state.open = open;
    }
};
export const economy = {
    namespaced,
    state,
    mutations
};
